<template>
  <div id="app">
    <el-row>
      <el-col :xs="24" :sm="24" :md="24" :lg="24">
        <div class="grid-content bg-purple">
          header
        </div>
      </el-col>
      <el-col :xs="4" :sm="4" :md="4" :lg="4">
        <ul class="grid-content bg-purple-dark">
          <li>
            <router-link to="/HistoricalEvents">历史事件</router-link>
          </li>
          <li>
            <router-link to="/AlarmEvents">报警事件</router-link>
          </li>
        </ul>
      </el-col>
      <el-col :xs="20" :sm="20" :md="20" :lg="20">
        <div class="grid-content bg-purple-light">
          <router-view></router-view>
        </div>
      </el-col>
    </el-row>

  </div>
</template>

<script>
import hello from './components/hello.vue'
import HistoricalEvents from './components/EventPages/HistoricalEvents.vue'
import AlarmEvents from './components/EventPages/AlarmEvents.vue'

export default {
  name: 'app',
  components: {
    hello,
    HistoricalEvents,
    AlarmEvents
  }
}
</script>

<style scoped>
html,
body,
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  height: 100%;
  margin: 0px;
  padding: 0px;
}

ul,
li,
ol {
  list-style: none;
  padding: 0;
  margin: 0;
}
div{
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
  color: #000000;
}

.bg-purple-dark {
  background: #99a9bf;
}

.bg-purple {
  background: #d3dce6;
}

.bg-purple-light {
  background: #e5e9f2;
}

.grid-content {
  border-radius: 0;
  min-height: 36px;
}
</style>



